import { getUserRealInfoSettledIn } from '@/api/userApi.js'
import { getUserRealInfo } from '@/api/userApi.js'
import T from '@/utils/tips.js'
export default {
	name: 'buyer',
	data() {
		return {
			name:'',
			number:'',
			code:'',
			isDoSubmit:true,
			titles:[
				{
					title:'专属代办',
					imgUrl:'../../../../static/imgs/buyer-1.png'
				},
				{
					title:'优先采购',
					imgUrl:'../../../../static/imgs/buyer-2.png'
				},
				{
					title:'实名认证',
					imgUrl:'../../../../static/imgs/buyer-3.png'
				}
			]
		};
	},
	components: {

	},
	mounted() {
		
	},
	methods: {
		checkSubmit(){
			if(this.code !='' && this.name !='' && this.number !=''){
				this.isDoSubmit = false
			}else{
				this.isDoSubmit = true
			}
		},
		doSubmit(){
			if(this.isDoSubmit) {
				return false
			}
			// 数据验证
			if(this.name == ''){
				T.tips('请输入用户名')
				return false
			}
			if(this.number == ''){
				T.tips('请输入身份证')
				return false
			}
			if(this.code == ''){
				T.tips('请输入邀请码')
				return false
			}
			
			// 实名认证
			let data = {
				realName:this.name,
				type: 3,
				cardNo:this.number,
				code:this.code
			}
			getUserRealInfoSettledIn(data).then(res => {
				if(res.code == '1000'){
					let realData = {
						userId: uni.getStorageSync('uid')
					}
					getUserRealInfo(realData).then(res => {
						if(res.code == '1000'){
							let roleId = res.data.userRole.roleId || '';
							uni.setStorageSync('roleId', roleId)
							uni.setStorageSync('userRealInfo',JSON.stringify(res.data.userRealInfo))
							uni.switchTab({
								url:'/pages/user/user'
							})
						}
					})
					
					
				}else{
					T.tips(res.message)
				}
			})
			
		}
	}
}
